@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@600&display=swap);
*{
    margin: 0;
    padding: 0;
    outline: 0;
}
body{
    background-color: #1f1f1ffc;
}

.app-container{
    padding: 20px 15px;
}

.title-container{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.title{
    color: #BF3312;
    font-size: 50px;
    font-family: 'Nunito', sans-serif;
    letter-spacing: 2px;
}

.page-divider{
    display: flex;
    width: 100%;
    margin-top: 25px;
}

@media screen and (max-width: 800px){
    .page-divider{
        display: flex;
        flex-direction: column;
        margin-top: 25px;
    }
}

.left-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-basis: 70%;
}

.right-container{
    display: flex;
    flex-direction: column;
    flex-basis: 25%;
    border: 2px solid #ffffff;
    height: 830px;
    border-radius: 10px;
    margin-left: 5px;
    background-color: #808080bf;
    justify-content: center;
}

.right-container-title{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 40px;
    padding: 10px 5px;

}

.ranking-title{
    color: #ffffff;
    font-family: 'Nunito', sans-serif;
}

.country-dropdown-container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-bottom: 30px;
}

#dropdown{
    width: 250px;
    height: 50px;
    border: 2px solid #ffebcd;
    border-radius: 10px;
    font-family: 'Nunito', sans-serif;
    font-size: 20px;
}

.country-status-container{
    display: flex;
    width: 95%;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 40px;
}


@media screen and (max-width: 900px){
    .country-status-container{
        display: flex;
        flex-direction: column;
        align-items: space-around;
        justify-content: space-between;
    }

}


@media screen and (max-width: 800px){
    .leaflet-container{
        height: 500px;
    }
}

.footer{
    display: flex;
    width: 100%;
    height: 50px;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    border-radius: 10px;
}

.footer-text{
    color: #1f1f1f;
    font-family: 'Nunito', sans-serif;
}

.heart{
    color: #E21717;
}

  
.table-container{
    height: 600px;
    overflow: auto;
    display: flex;
    justify-content: center;
}

.table{
    margin: 10px 15px;
}

.table-body{
    
}

tr{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    height: 20px;
    padding: 8px 5px;
    border-radius: 10px;
}

tr:nth-of-type(odd){
    background-color: #f0ffffcb;
}

td{
    font-family: 'Nunito', sans-serif;
}
.chart-container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 300px;
    border: 2px solid #ffffff;
    border-radius: 10px;
    box-shadow: 2px 2px 5px 10px #ffffff;
}

@media screen and(max-width: 900px) {
    .chart-container{
        width: 80%;
        margin-bottom: 30px;
    }
}



.leaflet-container{
    display: flex;
    justify-content: center;
    width: 92%;
    height: 400px;
    background-color: #ffffff;
    border-radius: 10px;
    margin-bottom: 30px;
    box-shadow: 2px 2px 5px 10px #ffffff;
}
